import React, { Component } from 'react';
import './App.css';
import Order from                     './Order';
import {Tabs, Tab} from               'material-ui/Tabs';
import AppBar from                    'material-ui/AppBar';
import FlatButton from                'material-ui/FlatButton';
import NavigationArrowBack from       'material-ui/svg-icons/navigation/arrow-back';


const styles = {
  appBar: {
    flexWrap: 'wrap',
    flexDirection: 'column',
    boxShadow: "none"
  },
  tabs: {
    width: '100%',
    boxShadow: "rgba(0, 0, 0, 0.117647) 0px 1px 6px, rgba(0, 0, 0, 0.117647) 0px 1px 4px"
  },
  backButton: {
    color: '#ffffff',
    fill: "#ffffff",
  },
  backButtonContainer : {
    alignSelf: "flex-start"
  },
  title : {
    textAlign: "left",
    marginLeft: 64
  }
};
class App extends Component {
  render() {
    return (
      <div className="App">
        <AppBar
         title="SYNKOPY"
         style={styles.appBar}
         iconStyleLeft={styles.backButtonContainer}
         titleStyle={styles.title}
         iconElementLeft={ <FlatButton
            label="Back To site"
            href="https://github.com/callemall/material-ui"
            style={styles.backButton }
            secondary={true}
            icon={<NavigationArrowBack />}
    />}
       >

    </AppBar>
    <Tabs tabItemContainerStyle={styles.tabs}>
      <Tab label="Brand designers">

      </Tab>
      <Tab label="graphic designers">

      </Tab>
      <Tab label="web designers">

      </Tab>
      <Tab label="icons">

      </Tab>
      <Tab label="templates">

      </Tab>
      <Tab label="inspiration">

      </Tab>
    </Tabs>

      </div>
    );
  }
}

export default App;



