import React, { Component } from 'react';
import './Order.css';
import Paper from 'material-ui/Paper';
import LabelGroup from './labelGroup';
import chunk from 'lodash/chunk';

const style = {
  width: '100%',
  height: '100%',
  display: 'flex',
  justifyContent: "space-between"
};
var labels = [
  {
    labelTitle : "customer",
    labelContent : "محمد عبدالرحمن",
    isArabic : true,
    isWhatsapp : false,
    isEmail : false,
    isPayment : false,
    paymentType : "",
    tagLink : "",
    tagLabel : ""
  },
  {
    labelTitle : "email",
    labelContent : "generic_email@gmail.com",
    isArabic : false,
    isWhatsapp : false,
    isEmail : false,
    isPayment : false,
    paymentType : "",
    tagLink : "mailto:generic_email@gmail.com",
    tagLabel : "email it!"
  },
  {
    labelTitle : "total checkout",
    labelContent : "4600RS",
    isArabic : false,
    isWhatsapp : false,
    isEmail : false,
    isPayment : false,
    paymentType : "",
    tagLink : "",
    tagLabel : ""
  },
  {
    labelTitle : "customer note",
    labelContent : "wrap it in a gift, and write \"your cousin mansour\"",
    isArabic : false,
    isWhatsapp : false,
    isEmail : false,
    isPayment : false,
    paymentType : "",
    tagLink : "",
    tagLabel : ""
  }
]

labels = chunk(labels, 2)
class Order extends Component {
  render() {
    var groups = []
    labels.map(function(subGroup, index ){
      return groups.push(<LabelGroup key={index} subGroup={subGroup} />)
    })
    return (
      <div className="Order">
      <Paper style={style} zDepth={1} >
        {groups}
      </Paper>
      </div>
    );
  }
}

export default Order;



